import React from 'react';
import api from '../../../Global/Api';
import { Form,Input,Radio,Button,Modal} from 'antd';

  
class RegistrationForm extends React.Component {
  constructor(props){
    super(props);
    this.initialState = {
      id:'',
      username:'',
      is_active:'',
      first_name:'',
      last_name:'',
      email:'',
      password: '',
    };
    if(this.props.userdetail){
      this.state = props.userdetail
    } else {
      this.state = this.initialState;
    }

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  success() {
    Modal.success({
      content: 'Successively Updated',
    });
  }
  
  error() {
    Modal.error({
      title: 'This is an error message',
      content: 'Error Occurred!!',
    });
  }
  

    handleSubmit = (e) => {
      e.preventDefault();
      this.props.form.validateFieldsAndScroll((err, value) => {
        if (!err) {
          console.log('Received values of form: ', value);
          this.setState({loading:true});
          if(this.props.isEditProduct){
          api.put(`/api/usercreate/${this.props.userdetail.id}`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 200){
                  this.success();
                  this.props.fetch();
              }
            })
            .catch(error => {
              this.error();
            });
          }
            else{
              api.post(`/api/usercreate`, value, {
            headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(value)})
            .then(response => {
              if(response.status === 201){
                  this.success();
                  this.props.fetch();
              }
            })
            .catch(error => {
              this.error();
            });
            }
          this.props.handleOk();
          this.setState(this.initialState);
         }
      });
    };

    componentWillReceiveProps(props){
    
      if(props.userdetail){
        this.state = props.userdetail;
      } else {
        this.state = this.initialState;
      }
    }


    render() {
      const { getFieldDecorator } = this.props.form;
  
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 8 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 },
        },
      };
      const tailFormItemLayout = {
        wrapperCol: {
          xs: {
            span: 24,
            offset: 0,
          },
          sm: {
            span: 16,
            offset: 8,
          },
        },
      };

      return (
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
        <Form.Item label="Username">
          {getFieldDecorator('username', { initialValue: this.state.username,
            rules: [
              {
                required: true,
              },
            ],
          })(<Input />)}
        </Form.Item>

          <Form.Item label="First Name">
            {getFieldDecorator('first_name', { initialValue: this.state.first_name,
              rules: [
                {
                  required: true,
                },
              ],
            })(<Input />)}
          </Form.Item>

          <Form.Item label="Last Name">
            {getFieldDecorator('last_name', { initialValue: this.state.last_name,
              rules: [
                {
                  required: true,
                },
              ],
            })(<Input />)}
          </Form.Item>

          <Form.Item label="Email">
            {getFieldDecorator('email', { initialValue: this.state.email,
              rules: [
                {
                  required: true,
                },
              ],
            })(<Input />)}
          </Form.Item>

          <Form.Item label="Password">
            {getFieldDecorator('password', { initialValue: this.state.password,
              rules: [
                {
                  required: true,
                },
              ],
            })(<Input.Password/>)}
          </Form.Item>
          
          <Form.Item label="Status" hasFeedback {...formItemLayout}>
            {getFieldDecorator('is_active', { initialValue: this.state.is_active,
              rules: [
                {
                  required: true,
                  type: 'boolean',
                },
              ],
            })(
              <Radio.Group name="radiogroup">
                <Radio value={true}>
                  Active
                </Radio >
                <Radio value={false}>
                  Disable
                </Radio >
              </Radio.Group>
            )}
          </Form.Item>
          

          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">
              Submit
            </Button>
          </Form.Item>
        </Form>
      );
    }
  }
  
  const WrappedRegistrationForm = Form.create({ name: 'register' })(RegistrationForm);
   
  export default WrappedRegistrationForm;
