import React from 'react';
import {Link} from 'react-router-dom';
import { Table, Input, Button, Icon, Modal, Divider, Popconfirm ,Switch} from 'antd';
import Highlighter from 'react-highlight-words';
import WrappedRegistrationForm from './components/RegistrationForm';
import api from '../../Global/Api';

class userList extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      selectedRowKeys: [],   //Check here to configure the default column
      loading: false,
      user: [],
      searchedColumn: '',
      userdetail:{},
      isEditProduct: false,
    };
  }

componentDidMount() {
    this.fetch();
}


fetch = (params = {}) => {
  this.setState({ loading: true });
  api.get(`/api/usercreate`,{}).then(res => {
      this.setState({
        loading: false,
        user: res.data,
      });
    });
};
  
  onSelectChange = selectedRowKeys => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleChange= async (id)=>{
    this.setState({ loading:true});
    await api.get(`/api/usercreate/${id}`, {})
    .then(res => {
      this.setState({
        userdata: res.data,
        visible: true,
        loading:false,
        isEditProduct:true
      });
    });
   };

   handleToggleStatus= async(id,is_active) =>{
    this.setState({ loading:true});
    await api.patch(`/api/usercreate/${id}`,{is_active: is_active?false:true,
         type: 'json',
     })
    .then(res => {
      this.setState({

      });
    });

   }

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,isEditProduct:false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,isEditProduct:false,
    });
  };

  handleBankDetail= async (id)=>{
    this.setState({ loading:true, 
      visible2: true, bankuser:id});
    await api.get(`/api/bank?user=${id}`, {})
    .then(res => {
      this.setState({
        bankdata: res.data,
        visible2: true,
        loading:false,
        isBanking:true,
        isEditProduct:true,
      });
    });
   };

  getColumnSearchProps = dataIndex => ({
      filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <div style={{ padding: 8 }}>
          <Input
            ref={node => {
              this.searchInput = node;
            }}
            placeholder={`Search ${dataIndex}`}
            value={selectedKeys[0]}
            onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
            onPressEnter={() => this.handleSearch(selectedKeys, confirm)}
            style={{ width: 188, marginBottom: 8, display: 'block' }}
          />
          <Button
            type="primary"
            onClick={() => this.handleSearch(selectedKeys, confirm)}
            icon="search"
            size="small"
            style={{ width: 90, marginRight: 8 }}
          >
            Search
          </Button>
          <Button onClick={() => this.handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reset
          </Button>
        </div>
      ),
      filterIcon: filtered => (
        <Icon type="search" style={{ color: filtered ? '#1890ff' : undefined }} />
      ),
      onFilter: (value, record) =>
        record[dataIndex]
          .toString()
          .toLowerCase()
          .includes(value.toLowerCase()),
      onFilterDropdownVisibleChange: visible => {
        if (visible) {
          setTimeout(() => this.searchInput.select());
        }
      },
      render: text => (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text.toString()}
        />
      ),
  });


  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex,
    });
  };

  handleReset = clearFilters => {
    clearFilters();
    this.setState({ searchText: '' });
  };


  render() {

    const columns = [{
      dataIndex: 'id',
      title: 'User Id',
      key:'Id',
      defaultSortOrder: 'ascend',
      sorter: (a, b) => a.id - b.id,
      },
      {
        dataIndex: 'username',
        title: 'Username',
        key: 'username',
        ...this.getColumnSearchProps('username'),
      },
      {
        dataIndex: 'first_name',
        title: 'Name',
        key: 'first_name',
      },
      {
        dataIndex: 'email',
        title: 'Email',
        key: 'email',
      },
      {
        dataIndex: 'is_active',
        title: 'Status',
        key: 'status' ,
        render: val => (val ? 'Active' : 'Inactive')
      },
      {
        title: 'Action',
        key: 'action',
        render: (record) => (
            <span>
            <Switch checked={record.is_active?true:false} onChange={() => this.handleToggleStatus(record.id,record.is_active)} />
            {/* <Popconfirm title="Sure to change status?" onConfirm={() => this.handleToggleStatus(record.id,record.is_active)}>
              <a>Change Status</a>
            </Popconfirm> */}
            <Divider type="vertical" />
            <Popconfirm title="Sure to edit?" onConfirm={() => this.handleChange(record.id)}>
              <a>Edit</a>
            </Popconfirm>
            <Divider type="vertical" />
            <Link to={`/bank/${record.id}`}>Bank</Link>
          </span>
          ),

      }
    ];

    const { loading, selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const hasSelected = selectedRowKeys.length > 0;
    return (
      
      <div>
        <div style={{ marginBottom: 16 }}>
          <Button type="primary"  disabled={!hasSelected} loading={loading}>
            Delete
          </Button>
          <Button type="primary" onClick={this.showModal} style={{float:"right"}}>
           Create
          </Button>
        <Modal
          title="User Login Form"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
        <WrappedRegistrationForm isEditProduct={this.state.isEditProduct} userdetail={this.state.userdata} handleOk={this.handleOk} fetch={this.fetch}/>
        </Modal>
          <span style={{ marginLeft: 8 }}>
            {hasSelected ? `Selected ${selectedRowKeys.length} items` : ''}
          </span>
        </div>
        <Table bordered rowSelection={rowSelection} columns={columns} dataSource={this.state.user} scroll={{ x: 'max-content' }}/>
      </div>
    );
  }
}
export default userList;





